import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';

import '../App.css';

import axios from 'axios';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


async function getUser(username, password) {
  try {
    const response = await axios.get('http://139.59.241.158/api/loginForChart', {
      params: {
        email: username,
        password: password,
      }
    });
    // console.log(response);
    return response;
  } catch (error) {
    console.error(error);
  }
}


function Login({ setUser }) {
  const [validated, setValidated] = useState(false);

  const handleSubmit = (event) => {
    event.preventDefault();
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      toast.error('Username and Password Must be provided', {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
      });
      event.stopPropagation();
    } else {
      // console.log(form.username.value);
      var result = getUser(form.username.value, form.password.value);
      result.then((data) => {
        console.log('result.data');
        console.log(data);
        if (data.data.message == 'success') {
          // var data = data.data
          // toast.success(data.data.message, {
          //   position: "top-right",
          //   autoClose: 5000,
          //   hideProgressBar: false,
          //   closeOnClick: true,
          //   pauseOnHover: true,
          //   draggable: true,
          //   progress: undefined,
          //   theme: "light",
          // });
          setValidated(true);
          sessionStorage.setItem('user', 'XDgre43Frwss');
          sessionStorage.setItem('watchList', data.data.symbolList);
          setUser('someUser');
          // alert(data.data.message)
        } else {
          toast.error(data.data.message, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
          });
        }
      })
    }

  };

  return (

    <div class="mx-auto col-12 col-md-8 col-lg-6">
      <h2 class="mt-5" style={{ color: 'white' }}>TradingView</h2>
      <hr></hr>
      <Form noValidate validated={validated} onSubmit={handleSubmit} data-bs-theme="dark" >

        <Form.Group as={Col} md="4" controlId="validationCustom01">
          <Form.Label style={{ color: 'white' }}>Username</Form.Label>
          <Form.Control
            required
            type="text"
            name='username'
            placeholder="username"

          />
          <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
        </Form.Group>
        <br></br>
        <Form.Group as={Col} md="4" controlId="validationCustom02">
          <Form.Label style={{ color: 'white' }}>Password</Form.Label>
          <Form.Control
            required
            type="Password"
            placeholder="password"
            name='password'
          />
          <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
        </Form.Group>
        <br />
        <Button type="submit">Login</Button>
      </Form>
      <ToastContainer
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
    </div>
  );
}

export default Login;