import React, { useEffect, useRef } from 'react';
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';

import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
// import MoreIcon from '@mui/icons-material/MoreVert';
// import MenuIcon from '@mui/icons-material/Menu';

const symbolOptions = [
  { value: 'AMEX:AAU', label: 'AMEX:AAU' },
  { value: 'NASDAQ:AAPL', label: 'NASDAQ:AAPL' },
  { value: 'NYSE:INFY', label: 'NYSE:INFY' }
]
let tvScriptLoadingPromise;
var tradingWidget;

var selectedSymbol = '';
var selectedInterval = '';
var selectedInterval = '';


export default function TradingViewWidget() {
  const onLoadScriptRef = useRef();
  const handleSymbolChange = (e) => {
    console.log(e.value)
    // alert(e.value)
    // tradingWidget.symbol = e.value
    document.getElementById('tradingview_a5e6d').innerHTML = ''
    tradingWidget = new window.TradingView.widget({
      // autosize: true,
      width: "100%",
      height: "768",
      symbol: e.value,
      interval: "W",
      timezone: "Etc/UTC",
      theme: "dark",
      style: "1",
      locale: "in",
      toolbar_bg: "#f1f3f6",
      enable_publishing: false,
      withdateranges: true,
      hide_side_toolbar: false,
      watchlist: sessionStorage.getItem('watchList').split(',')??[],
      details: false,
      hotlist: false,
      calendar: false,
      // show_popup_button: true,
      // popup_width: "1000",
      // popup_height: "650",
      allow_symbol_change: false,
      container_id: "tradingview_a5e6d"
    });
  }

  useEffect(
    () => {
      onLoadScriptRef.current = createWidget;

      if (!tvScriptLoadingPromise) {
        tvScriptLoadingPromise = new Promise((resolve) => {
          const script = document.createElement('script');
          script.id = 'tradingview-widget-loading-script';
          script.src = 'https://s3.tradingview.com/tv.js';
          script.type = 'text/javascript';
          script.onload = resolve;

          document.head.appendChild(script);
        });
      }

      tvScriptLoadingPromise.then(() => onLoadScriptRef.current && onLoadScriptRef.current());

      return () => onLoadScriptRef.current = null;

      function createWidget() {
        if (document.getElementById('tradingview_a5e6d') && 'TradingView' in window) {
          tradingWidget = new window.TradingView.widget({
            // autosize: true,
            width: "100%",
            height: "768",
            symbol: "AMEX:AAU",
            interval: "W",
            timezone: "Etc/UTC",
            theme: "dark",
            style: "1",
            locale: "in",
            toolbar_bg: "#f1f3f6",
            enable_publishing: false,
            withdateranges: true,
            hide_side_toolbar: false,
            watchlist: sessionStorage.getItem('watchList').split(',')??[],
            details: false,
            hotlist: false,
            calendar: false,
            // show_popup_button: true,
            // popup_width: "1000",
            // popup_height: "650",
            allow_symbol_change: false,
            container_id: "tradingview_a5e6d"
          });
        }
      }
    },
    []
  );

  return (
    <div style={{marginTop: '10px'}}>
     <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static">
        <Toolbar variant="">
          {/* <IconButton edge="start" color="inherit" aria-label="menu" sx={{ mr: 2 }}>
          </IconButton> */}
          {/* <Typography variant="h6" color="inherit" component="div"> */}
              {/* <Autocomplete
                disablePortal
                id="combo-box-demo"
                options={symbolOptions}
                sx={{ width: 200 }}
                onChange={e => handleSymbolChange(e)}
                renderInput={(params) => <TextField {...params} label="SYMBOL" />}
              /> */}
          {/* </Typography> */}
          {/* <Box sx={{ display: { xs: 'flex', md: 'none' } }}> */}
            {/* <IconButton
              size="large"
              aria-label="show more"
              // aria-controls={mobileMenuId}
              aria-haspopup="true"
              // onClick={handleMobileMenuOpen}
              color="inherit"
            >
              dadasqd
            </IconButton> */}
          {/* </Box> */}
        </Toolbar>
        
      </AppBar>
    </Box>
      
              
              {/* <Select options={symbolOptions} styles={{ color: 'black'}} onChange={e => handleSymbolChange(e)} /> */}
            
              
     
      
      {/* <div>Logout</div> */}
      <div className='tradingview-widget-container'>
        <div id='tradingview_a5e6d' />
      </div>
      <div style={{
        width: '37px',
        backgroundColor: 'white',
        height: '37px',
        marginTop: '-118px',
        marginLeft: '69px',
        marginRight: 'auto',
        position: 'relative',
        borderRadius: '10px'
      }}></div>
    </div>
  );
}
